import Complex from './Complex.js';

/**
 * Tworzy tablic z argumentami typu Complex
 */
export default class ComplexArray extends Array {
	/**
   * Jeeli size > 0, tworzy tablic n-elementow wypenion instancjami klasy Complex
   * @param {Number} size rozmiar tablicy
   */
	constructor(size = 0) {
		super();
		if (size < 1) return this;
		for (let i = 0; i < size; i += 1) {
			this.push(new Complex(0, 0));
		}
	}

	slice(start, end) {
		return [...this].slice(start, end);
	}

	/**
   * Umieszcza element w tablicy
   * @param {Complex} complex
   * @returns {ComplexArray} this
   * @throws {Error} niepoprawny typ argumentu
   */
	push(complex) {
		if (!(complex instanceof Complex))
			throw new Error('Wrong type of complex argument, expected Complex type');
		super.push(complex);
		return this;
	}

	/**
   * Gboka kopia
   * @returns {ComplexArray} kopia
   */
	deepCopy() {
		const result = new ComplexArray();
		super.forEach((complex) => result.push(complex.copy()));
		return result;
	}

	/**
   * Przepisuje tablic bez zachowania referencji, gbokie kopiowanie
   * @param {ComplexArray} complexArray tablica do przepisania
   * @returns {ComplexArray} this
   * @throws {Error} Niepoprawny typ argumentu
   * @throws {Error} Niepoprawny rozmiar kopiowanej tablicy
   */
	rewrite(complexArray) {
		if (!(complexArray instanceof ComplexArray))
			throw new Error('Argument must be the type of ComplexArray');
		if (this.length !== complexArray.length)
			throw new Error(
				`Mismatch of length. Length = ${complexArray.length}, expected ${this.length}.`
			);
		for (let i = 0; i < this.length; i += 1) {
			this[i].init(complexArray[i]);
		}
		return this;
	}

	/**
   * Ustawia rozmiar tablicy na 0
   * @returns {ComplexArray} this
   */
	clear() {
		super.length = 0;
		return this;
	}

	/**
   * Jeeli pusta tablica zwraca true
   * @returns {boolean}
   */
	isEmpty() {
		return super.length === 0;
	}
}

/**
 * Tworzy ComplexArray ze zwykego array
 * @param {Array} array
 * @returns {ComplexArray} complexArray
 * @throws {Error} array nie jest zwyk tablic
 */
ComplexArray.createFromArray = (array) => {
	if (!array || array.constructor.name !== 'Array')
		throw Error('Wrong type of array argument, expected simple Array object.');
	const result = new ComplexArray();
	const l = array.length;
	for (let i = 0; i < l; i += 1) {
		result.push(new Complex(array[i], 0));
	}
	return result;
};
